













import Vue, { PropType } from 'vue';

type Value = number | string;

export default Vue.extend({
  name: 'Setting',
  props: {
    label: {
      type: String,
      required: true
    },
    value: {
      type: [Number, String] as PropType<Value>,
      required: true
    },
    options: {
      type: Array as PropType<Value[]>,
      required: true
    }
  }
});
